body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", 'Orbitron',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.container {
	margin: 15px;
	box-sizing: border-box;
	text-align: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-direction: row;
	        flex-direction: row;

}

.hidden {
	display: none;
}

.timer-container {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
	-webkit-flex-direction: row;
	        flex-direction: row;
}

.current-timer {
	border: 2px solid rgb(5, 14, 39);
	border-radius: 60%;
	padding: 50px;
	margin: 10px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	font-size: 40px;
	background-color: rgb(255, 255, 255);
}

.controls {
	padding: 5px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: row;
	        flex-direction: row;
}

.timer-controls {
	padding: 5px;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
}

.btn-secondary {
	display: -webkit-flex;
	display: flex;
	height: 50px;
}

.clock {
	display: -webkit-flex;
	display: flex;
	font-family: 'Orbitron', sans-serif;
	-webkit-flex-direction: column;
	        flex-direction: column;
	-webkit-justify-content: center;
	        justify-content: center;
	text-align: center;
	-webkit-align-items: center;
	        align-items: center;


}
